import React, { Component } from 'react';

class ToDoItem extends Component {
  state = { 
    task: this.props.task,
    completed: this.props.completed,
    id: this.props.id
  }

  whichListLayout = () => {
    if (this.state.completed === false) {
      return (
        <li style={{marginTop: "15px"}}>
          <span className="listSpan">{this.state.task}</span>

          <button className="listBtn1" 
            onClick={() => this.props.deleteItem(this.state.id)}>Delete
          </button>

          <button className="listBtn2" 
            onClick={() => this.props.finished(this.state.id)}>Completed
          </button>
        </li>
      )
    }

    return <li 
            style={{marginTop: "15px"}}>
            <span className="listSpan">{this.state.task}</span>
          </li>
  }

  render() { 
    return <React.Fragment>{this.whichListLayout()}</React.Fragment>;
  }
}
 
export default ToDoItem;